import React from 'react';
import './navbar.css';

class Navbar extends React.Component {
  constructor() {
    super();
    this.state = {
      showMenu: 'menu',
      navColored: null
    }
    this.showMenu = this.showMenu.bind(this);
    this.handleScroll = this.handleScroll.bind(this);
    this.closeMenu = this.closeMenu.bind(this);
  }

  componentDidMount() {
    window.addEventListener('scroll', this.handleScroll);
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll);
  }

  handleScroll() {
    let color;

    if (window.scrollY > 250) {
      color = 'navColored';
    } else {
      color = null;
    }

    this.setState({
      navColored: color
    });
  }

  showMenu() {
    if (this.state.showMenu === 'menu') {
      this.setState({showMenu: 'menu showMenu'});
    } else {
      this.setState({showMenu: 'menu'});
    }
  }

  // to make sure menu is closed
  // when list click is occur
  closeMenu(){
    this.setState({showMenu: 'menu'});
  }

  render(){
    return (
        <nav className={this.state.navColored ? 'navColored' : undefined}>
          <h1 className='title'>{this.props.title}</h1>
          <ul className={this.state.showMenu}>
          {
            Object.entries(this.props.data).map(([key, value]) => {
              return <li key={key}><a href={'#'+key} onClick={this.closeMenu}>{value}</a></li>
            })
          }
            <li><a href='https://fauzanashariblog.blogspot.com' target='_blank' onClick={this.closeMenu}>Blog</a></li>
          </ul>
          <div id='hamburger' onClick={this.showMenu}>
            <span></span>
            <span></span>
            <span></span>
          </div>
        </nav>
    );
  }
}

export default Navbar;
